import axios from 'axios'
import {
  Toast
} from 'vant'
import store from "@/store";
// http://live.sss2.cc
// http://192.168.3.20:9104
let loadingInstance = null //这里是loading
//使用create方法创建axios实例
export const ServiceJava = axios.create({
  timeout: 0, // 请求超时时间
  baseURL: process.env.VUE_APP_BASE_API_JAVA,
  headers: {
    'Content-Type': 'application/json'
  }
})
// 添加请求拦截器
ServiceJava.interceptors.request.use(config => {
  // if (config.loading != false) {
  //   loadingInstance = Toast.loading({
  //     duration: 0,
  //     message: '加载中...',
  //     forbidClick: true
  //   });
  // }
  config.headers.Authorization = localStorage.getItem('token')
  config.headers.userId = localStorage.getItem('userId')
  return config
})
// 添加响应拦截器
ServiceJava.interceptors.response.use(response => {
  // if (loadingInstance) loadingInstance.clear()
  let data = response.data
  if (data.code == 200) {
    return data
  } else {
    Toast(data.msg)
  }

}, error => {
  let msg = ''
  console.log('TCL: error', error.response)
  let res = error.response
  if (res.data.code === 400) {
    msg = res.data.data.msg
  } else {
    msg = error.msg !== undefined ? error.msg : '网络错误'
  }
  Toast({
    forbidClick: true,
    message: msg
  });
  return Promise.reject(error)
})
